@import "ScssHelpers/helpers-all.scss";

.cuts_info__bg {
	z-index: -1;
	position: absolute;
	top: 0;
	left: 0;

	width: 100%;
	height: 100%;
}

.cuts_info__bg_img {
	display: block;
	width: 100%;
	height: 100%;
	object-fit: cover;
}

.cuts_info__parts_control_wrap {
	position: relative;

	@include bp($point_2, min) {
		margin-bottom: 3.2rem;
	}

	@include bp($point_3) {
		margin-bottom: .4rem;
	}
}

.cuts_info__parts_control {
	max-width: 67.6rem;
	width: 100%;
	margin: 0 auto;
	background-color: $white;

	@include bp($point_2, min) {
		padding: 2rem 0;
	}

	@include bp($point_3) {
		padding: 1rem 0;
	}
}

.cuts_info__parts_control_img {
	position: relative;
	padding-top: 62%;
}

.cuts_info__parts_control_svg {
	position: absolute;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;

	display: block;
	width: 100%;
	height: 100%;
	object-fit: cover;
}

.cuts_info__area {
	color: transparent;
	transition: color 0.3s ease;
	cursor: pointer;

	&.cuts_info__area--active_state {
		color: $green;
	}
}

.cuts_info__label {
	color: $gray_10;
	transition: color 0.3s ease;
	pointer-events: none;

	&.cuts_info__label--active_state {
		color: $white;
	}
}

.cuts_info__container {
	background-color: $white;

	@include bp($point_2, min) {
		padding: 2.8rem 7.2rem 8.8rem;
	}

	@include bp($point_3) {
		padding: 1.2rem 2.4rem 3rem;
	}
}

.cuts_info__slider_body {
	@include bp($point_2, min) {
		padding-top: 1.8rem;
	}
}

.cuts_info__controls {

}

.cuts_info__button {
	position: absolute;
	top: 50%;

	@include bp($point_2, min) {
		margin-top: -4.4rem;

		&.cuts_info__button--prev_mod {
			left: -0.5rem;
		}

		&.cuts_info__button--next_mod {
			right: -0.5rem;
		}
	}

	@include bp($point_3) {
		margin-top: -2.2rem;

		&.cuts_info__button--prev_mod {
			left: -4.5rem;
		}

		&.cuts_info__button--next_mod {
			right: -4.5rem;
		}
	}
}

.cuts_info__content {
	&:not(.cuts_info__content--active_state) {
		display: none;
	}
}

.cuts_info__row {
	@include bp($point_2, min) {
		@include flex_block(row, wrap, flex-start);
		margin: 0 -5.6rem -5.6rem 0;
	}
}

.cuts_info__col {
	@include bp($point_2, min) {
		padding: 0 5.6rem 5.6rem 0;

		&:first-child {
			width: 56.3%;
		}
		&:last-child {
			width: 43.7%;
			padding-top: 1.1rem;
		}
	}

	@include bp($point_3) {
		&:last-child {
			display: none;
		}
	}
}

.cuts_info__title {
	@include h3;

	@include bp($point_2, min) {
		&:not(:last-child) {
			margin-bottom: 1rem;
		}
	}

	@include bp($point_3) {
		&:not(:last-child) {
			margin-bottom: .6rem;
		}
	}
}

.cuts_info__descr {
	@include text;

	@include bp($point_2, min) {
		line-height: 1.5;

		&:not(:last-child) {
			margin-bottom: 2.8rem;
		}
	}

	@include bp($point_3) {
		line-height: 1.42;
		letter-spacing: -.02em;

		&:not(:last-child) {
			margin-bottom: 2rem;
		}
	}
}

.cuts_info__post_title {
	@include h4;

	letter-spacing: -0.4px;

	&:not(:last-child) {
		margin-bottom: 1.5rem;
	}
}

.cuts_info__list_w {
	&:not(:last-child) {
		margin-bottom: 4.4rem;
	}

	@include bp($point_3) {
		display: none;
	}
}

.cuts_info__list {
	@include flex_block(row, wrap, flex-start);

	margin: 0 -4rem -4rem 0;
}

.cuts_info__item {
	width: calc(100% / 3);
	padding: 0 4rem 4rem 0;
}

.cuts_info__item_title {
	color: $gray_2;
	font-size: 1.8rem;
	font-weight: 500;
	letter-spacing: 2px;
	line-height: 133%;
	text-transform: uppercase;

	&:not(:last-child) {
		margin-bottom: 0.8rem;
	}
}

.cuts_info__item_descr {
	font-size: 1.4rem;
	font-weight: 400;
	line-height: 1.5;
}

.cuts_info__icon {
	display: block;
	width: 6.4rem;
	height: 6.4rem;

	border: 1px solid $gold_2;
	border-radius: 50%;

	&:not(:last-child) {
		margin-bottom: 1.2rem;
	}
}

.cuts_info__media {
	position: relative;
	width: 100%;
	@include bp($point_2, min) {
		padding-bottom: 61.6%;
	}

	@include bp($point_3) {
		padding-bottom: 61%;
	}
}

.cuts_info__video_trigger {
	position: absolute;
	left: 50%;
	top: 50%;
	color: $white;

	@include bp($point_2, min) {
		margin: -6rem 0 0 -6rem;
		width: 12rem;
		height: 12rem;
	}

	@include bp($point_3) {
		margin: -3.6rem 0 0 -3.6rem;
		width: 7.2rem;
		height: 7.2rem;
	}
}

.cuts_info__img_wrap {
	@include stretch;
}

.cuts_info__img {
	@include cover_img;
}

.cuts_info__post_list {
	margin: 0 0 -3.2rem 0;
}

.cuts_info__post_list_item {
	padding: 0 0 3.2rem 0;
}
